
// Queryによって全記事を取得し、ループで各記事情報をProps経由で渡し、<ChatLine />をレンダリングする

import React, { Component } from "react";
import ChatLine from "./ChatLine";

import gql from "graphql-tag";
import { graphql } from "react-apollo";

class ChatList extends Component {
  componentDidMount() {
    this.props.allChatsQuery.subscribeToMore({
      document: CHATS_SUBSCRIPTION,
      updateQuery: (prev, { subscriptionData }) => {
        return { allChats: [
          ...prev.allChats,
          subscriptionData.data.Chat.node,
        ]};
      },
    });
  }

  render() {
    const query = this.props.allChatsQuery;

    if (query && query.loading) {
      return <p>データを読み込み中</p>;

    } else if (query && query.error) {
      return <p>エラーが発生しました。</p>;

    }

    return (
      <div id="chat-list">
        {query.allChats.map(chat => <ChatLine
          key={chat.id}
          name={chat.name}
          datetime={chat.datetime}
          content={chat.content}
        />)}
      </div>
    );
  }

  componentDidUpdate() {
    const chatList = document.getElementById("chat-list");
    if (chatList) {
      chatList.scrollTop = ChatLine.HEIGHT * this.props.allChatsQuery.allChats.length;
    }
  }
}

const ALL_CHATS_QUERY = gql`
  query {
    allChats(last: 5) {
      id
      name
      datetime
      content
    }
  }
`;

const CHATS_SUBSCRIPTION = gql`
  subscription updateChat {
    Chat(
      filter: {
        mutation_in: [CREATED] # 購読対象のmutationの種類を指定
      }
    ) {
      mutation # 実行されたmutationの種類を出力
      node { # 変化後のデータを出力
        id
        name
        datetime
        content
      }
    }
  }
`;

export default graphql(ALL_CHATS_QUERY, { name: "allChatsQuery" })(ChatList);



