
// Mutationによって新規記事情報を追加保存する

import React, { Component, Fragment } from "react";
import gql from "graphql-tag";
import { graphql } from "react-apollo";

class ChatForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: null,
      content: ''
    }
  }

  componentDidMount() {
    // チャット入力してエンター
    const chatContent = document.getElementById('chat-content');
    chatContent.addEventListener('keypress', e => {
      if (e.keyCode == '13') {
        this.createChat();
      }
    });
  }

  createChat = async () => {
    if (this.state.content.length < 1) {
      return;
    }

    const name = this.props.name;
    const datetime = toLocaleString(new Date());
    const content = this.state.content;

    try {
      await this.props.createChatMutation({
        variables: {
          name,
          datetime,
          content
        }
      });
      this.setState({ content: '' });
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    return (
      <Fragment>
        <div className="input-group">
          <input
            id="chat-content"
            type="text"
            className="form-control"
            value={this.state.content}
            placeholder="チャットを入力してね"
            onChange={e => this.setState({ content: e.target.value })}
          />
          <div className="input-group-append">
            <button
            className="btn btn-primary"
            onClick={e => this.createChat(e)}
            >送信</button>
          </div>
        </div>
      </Fragment>
    );
  }
}

const toLocaleString = (date) => {
  return [
    date.getFullYear(),
    date.getMonth() + 1,
    date.getDate()
    ].join( '/' ) + ' '
    + date.toLocaleTimeString();
}

const CREATE_CHAT_MUTATION = gql`
  mutation createChatMutation($name: String, $datetime: String, $content: String) {
    createChat(name: $name, datetime: $datetime, content: $content) {
      id
    }
  }
`;

export default graphql(CREATE_CHAT_MUTATION, {
name: "createChatMutation"
})(ChatForm);



