
import { ApolloProvider } from 'react-apollo';
import { ApolloClient } from 'apollo-client';
import { HttpLink, InMemoryCache } from 'apollo-client-preset';

import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components/App';
import registerServiceWorker from './registerServiceWorker';
import { split } from 'apollo-link';
import { WebSocketLink } from 'apollo-link-ws';
import { getMainDefinition } from 'apollo-utilities';

const GRAPHQL_ENDPOINT =  'https://api.graph.cool/simple/v1/cjibfc8fw6ywj0130esgz65w9';
const SUBSCRIPTIONS_ENDPOINT = 'wss://subscriptions.graph.cool/v1/cjibfc8fw6ywj0130esgz65w9';

if (!GRAPHQL_ENDPOINT) {
  throw Error('GRAPHQL_ENDPOINTが設定されていません。');
}

if (!SUBSCRIPTIONS_ENDPOINT) {
  throw Error('SUBSCRIPTIONS_ENDPOINTが設定されていません。');
}

const wsLink = new WebSocketLink({
  uri: SUBSCRIPTIONS_ENDPOINT,
  options: {
    reconnect: true
  }
});

const httpLink = new HttpLink({
  uri: GRAPHQL_ENDPOINT
});

const link = split(
  ({ query }) => {
    const { kind, operation } = getMainDefinition(query);
    return kind === 'OperationDefinition' &&
           operation === 'subscription';
  },
  wsLink,
  httpLink,
);

const client = new ApolloClient({
  link: link,
  cache: new InMemoryCache()
});

const ApolloApp = (
  <ApolloProvider client={client}>
    <App />
  </ApolloProvider>
);

ReactDOM.render(ApolloApp, document.getElementById('root'));
registerServiceWorker();



