
// 単体の記事情報（タイトルおよび内容）をレンダリングする

import React, { Component } from "react";

class Chat extends Component {
  static HEIGHT = 25;
  render() {
    return (
      <div className="chat-line">
        <div className="chat-line-name">{this.props.name}</div>
        <div className="chat-line-comma">:</div>
        <div className="chat-line-datetime">{this.props.datetime}</div>
        <div className="chat-line-comma">:</div>
        <div className="chat-line-content">{this.props.content}</div>
      </div>
    );
  }
}

export default Chat;



