
// Queryによって全マスを取得し、ループで各マス情報をProps経由で渡し、<Cell />をレンダリングする

import React, { Component, Fragment } from 'react';
import gql from 'graphql-tag';
import { graphql } from 'react-apollo';

import Game from "./Game";
import Cell from './Cell';

class Board extends Component {
  componentDidMount() {
    this.props.allPlacementsQuery.subscribeToMore({
      document: PLACEMENTS_SUBSCRIPTION,
      updateQuery: (prev, { subscriptionData }) => {
        let result = { allPlacements: prev.allPlacements };
        const subplacements = subscriptionData.data.Placement;

        if (subplacements.mutation === 'CREATED') {
          // 作成Subscriptionきたら、新しい碁石を足す
          const newPlacements = [
            ...prev.allPlacements,
            subplacements.node,
          ];
          result = { allPlacements: newPlacements };

        } else if (subplacements.mutation === 'DELETED') {
          // 削除Subscriptionきたら、置いてある碁石をフィルタして消す
          result = {
            allPlacements: result.allPlacements.filter(
              (placement, index) => placement.id != subplacements.previousValues.id
            )
          };
        }

        return result;
      },
    });
  }

  render() {
    let message, cellElements;

    const query = this.props.allPlacementsQuery;
    const allPlacements = query.allPlacements;
    if (allPlacements) {
      // マス分の配列を用意して、碁石があるマスだけboolが入る
      let cells = Array.from(new Array(Game.WIDTH * Game.HEIGHT)).map(() => Game.COLOR_NONE);
      for (const placement of allPlacements) {
        cells[placement.x + placement.y * Game.WIDTH] = placement.color;
      }

      // 次の色を決める
      const nextColor = allPlacements.length % 2 === 0 ? Game.COLOR_BLACK : Game.COLOR_WHITE;

      // 自分の色を決める
      let myColor;
      if (this.props.gameMode == Game.MODE_SELF) {
        myColor = nextColor;
      } else {
        myColor =
          this.props.gameMode == Game.MODE_BLACK ?
            Game.COLOR_BLACK :
            this.props.gameMode == Game.MODE_WHITE ?
              Game.COLOR_WHITE :
              Game.COLOR_NONE;
      }

      let skip = true;

      // メッセージ生成
      if (query && query.loading) {
        message = <p>碁石読み込み中...</p>;

      } else if (query && query.error) {
        message = <p>碁石読み込み中にエラーが発生しました。</p>;

      } else if (this.props.isSending) {
        message = <p>碁石送信中...</p>;

      } else if (this.props.isPlayerSending) {
        message = <p>プレイヤー情報送信中...</p>;

      } else if (nextColor != myColor) {
        message = <p>相手の番です。</p>;

      } else {
        // 自分の番で送信中でなければ碁石を置ける
        skip = false;
      }

      cellElements = cells.map((color, index) => {
        return (
          <Cell
            key={index}
            color={color}
            onClick={(e, x, y) => this.props.onClick(
              e,
              // まだ碁石が置いてなければ、碁石を置ける
              color === Game.COLOR_NONE ? skip : true,
              myColor,
              x,
              y
            )}
            x={index % Game.WIDTH}
            y={Math.floor(index / Game.HEIGHT)}
          />
        );
      });
    }

    return (
      <Fragment>
        <div
          id="game-board"
          style={
            this.props.isDescRead ?
            null :
            {display:'none'}
          }
        >
          {cellElements}
        </div>
        <div className="game-message">
          {message}
        </div>
      </Fragment>
    );
  }
}

const ALL_PLACEMENTS_QUERY = gql`
  query {
    allPlacements {
      id
      color
      x
      y
    }
  }
`;

const PLACEMENTS_SUBSCRIPTION = gql`
  subscription updatePlacement {
    Placement(
      filter: {
        mutation_in: [CREATED, UPDATED, DELETED] # 購読対象のmutationの種類を指定
      }
    ) {
      mutation # 実行されたmutationの種類を出力
      node { # 変化後のデータを出力
        id
        color
        x
        y
      }
      updatedFields # UPDATEされたフィールドを出力
      previousValues { # 変化前のデータを出力
        id
        color
        x
        y
      }
    }
  }
`;

export default graphql(ALL_PLACEMENTS_QUERY, { name: 'allPlacementsQuery' })(Board);



