
// Mutationによって新規マス情報と新規プレイヤー情報を追加保存する

import React, { Component, Fragment } from 'react';
import gql from 'graphql-tag';
import { graphql, compose } from 'react-apollo';

import Board from './Board';
import Chat from "./Chat";
import ChatForm from "./ChatForm";

class Game extends Component {
  static WIDTH = 15;
  static HEIGHT = 15;

  static COLOR_NONE = null;
  static COLOR_BLACK = false;
  static COLOR_WHITE = true;

  static MODE_SELF = 1;
  static MODE_BLACK = 2;
  static MODE_WHITE = 3;
  static MODE_SPECTATOR = 4;

  static NAME_DEFAULT = '名無しさん';
  static NAME_DEFAULT_BLACK = '黒さん';
  static NAME_DEFAULT_WHITE = '白さん';

  constructor(props) {
    super(props);
    this.state = {
      isDescRead: false,
      isPlacementSending: false,
      isPlayerSending: false,
      gameMode: Game.MODE_SELF,
      playerName: Game.NAME_DEFAULT
    };
  }

  componentDidMount() {
    // プレイヤー情報のサブスクリプション
    this.props.playersQuery.subscribeToMore({
      document: PLAYERS_SUBSCRIPTION,
      updateQuery: (prev, { subscriptionData }) => {
        const node = subscriptionData.data.Player.node;
        const result =
          node.color == Game.COLOR_NONE ?
            null :
            node.color == Game.COLOR_BLACK ?
              { blackPlayer: [{ name: node.name }] } :
              { whitePlayer: [{ name: node.name }] };
        return result;
      },
    });

    // マス情報のサブスクリプション
    this.props.allPlacementsQuery.subscribeToMore({
      document: PLACEMENTS_SUBSCRIPTION,
      updateQuery: (prev, { subscriptionData }) => {
        let result = { allPlacements: prev.allPlacements };
        const subPlacements = subscriptionData.data.Placement;

        if (subPlacements.mutation === 'DELETED') {
          // 削除Subscriptionきたら、置いてある碁石をフィルタして消す
          result = {
            allPlacements: result.allPlacements.filter(
              (placement, index) => (placement && subPlacements.previousValues) ?
                placement.id != subPlacements.previousValues.id :
                false
            )
          };
        }

        return result;
      },
    });

    // プレイヤー名入力してエンター
    const playerName = document.getElementById('player-name');
    playerName.addEventListener('keypress', e => {
      if (e.keyCode == '13') {
        this.createPlayer();
      }
    });
  }

  createPlacement = async (e, skip, color, x, y) => {
    e.preventDefault();
    if (skip) {
      return;
    }
    if (judgeGomoku(this.props.allPlacementsQuery.allPlacements) != Game.COLOR_NONE) {
      return;
    }

    this.setState({
      isPlacementSending: true
    });

    try {
      await this.props.createPlacementMutation({
        variables: {
          color,
          x,
          y
        }
      });
      this.setState({
        isPlacementSending: false
      });
    } catch (err) {
      console.error(err);
    }
  };

  createPlayer = async () => {
    this.setState({
      isPlayerSending: true
    });

    const color =
      this.state.gameMode == Game.MODE_BLACK ?
        Game.COLOR_BLACK :
        this.state.gameMode == Game.MODE_WHITE ?
          Game.COLOR_WHITE :
          Game.COLOR_NONE;

    const name = this.state.playerName;

    try {
      await this.props.createPlayerMutation({
        variables: {
          color,
          name
        }
      });
      this.setState({
        isPlayerSending: false
      });
    } catch (err) {
      console.error(err);
    }
  };

  resetBoard = async e => {
    e.preventDefault();
    this.setState({
      isResetting: true
    });
    try {
      const allPlacements = JSON.parse(JSON.stringify(this.props.allPlacementsQuery.allPlacements));
      for (const placement of allPlacements){
        const id = placement.id;
        await this.props.ResetBoardMutation({
          variables: {
            id
          }
        });
      }
      this.setState({
        isResetting: false
      });
    } catch (err) {
      console.error(err);
    }
  }

  readDesc = e => {
    e.preventDefault();
    this.setState({
      isDescRead: true
    });
  }

  render() {
    const query = this.props.playersQuery;

    // メッセージ生成
    let message;
    if (query && query.loading) {
      message = <p>プレイヤーデータを読み込み中...</p>;

    } else if (query && query.error) {
      message = <p>プレイヤー読み込みでエラーが発生しました。</p>;

    }

    let blackPlayerName = Game.NAME_DEFAULT_BLACK;
    if (query.blackPlayer) {
      blackPlayerName = query.blackPlayer.length > 0 ? query.blackPlayer[0].name : Game.NAME_DEFAULT_BLACK;
    }
    let whitePlayerName = Game.NAME_DEFAULT_WHITE;
    if (query.whitePlayer) {
      whitePlayerName = query.whitePlayer.length > 0 ? query.whitePlayer[0].name : Game.NAME_DEFAULT_WHITE;
    }

    const winner = this.props.allPlacementsQuery.allPlacements ?
      judgeGomoku(this.props.allPlacementsQuery.allPlacements):
      Game.COLOR_NONE;

    return (
      <Fragment>
        <div id="game-title">Gomoku</div>
        <div id="game-desc" style={
          this.state.isDescRead ?
          {display:'none'} :
          null
        }>
          <div>まずは、ゲームモードを選んでね。</div>
          <div>「ひとりモード」と「オンライン対戦モード」の先手か後手かを選べるよ</div>
          <div>オンライン対戦の場合は、先手後手を選んだあと、名前を登録することができるよ</div>
          <div>リセットボタンは誰でも押せるけど、盤面はみんなで同期しているから、気をつけてね</div>
          <button
           className="btn btn-primary"
           onClick={e => this.readDesc(e)}
          >わかった ！ ゲームスタート ！</button>
        </div>
        <div id="game-setting">
          <div className="input-group">
            <div className="input-group-prepend">
              <button
                className="btn btn-danger"
                id="game-reset"
                onClick={e => this.resetBoard(e)}
              >リセット</button>
            </div>
            <select
              id="game-mode"
              className="custom-select"
              onChange={e => this.setState({ gameMode: e.target.value })}
            >
              <option value={Game.MODE_SPECTATOR}>観戦モード</option>
              <option value={Game.MODE_SELF}>ひとりモード</option>
              <option value={Game.MODE_BLACK}>オンライン対戦　先手（黒）</option>
              <option value={Game.MODE_WHITE}>オンライン対戦　後手（白）</option>
            </select>
            <input
              id="player-name"
              type="text"
              className="form-control"
              placeholder="プレイヤー名を入力してください。"
              onChange={e => this.setState({ playerName: e.target.value })}
              value={this.state.playerName}
            />
            <div className="input-group-append">
              <button
                className="btn btn-info"
                id="game-start"
                onClick={e => this.createPlayer()}
              >登録</button>
            </div>
          </div>
        </div>
        <div
          id="game-players"
          style={
            this.state.isDescRead ?
            null :
            {display:'none'}
          }
        >
          <div
            className={winner == Game.COLOR_NONE ?
              'game-player' :
              winner == Game.COLOR_BLACK ?
                'game-player-winner' :
                'game-player'
            }
          >{blackPlayerName}</div>
          <div
            className="game-player-vs"
          >vs</div>
          <div
            className={winner == Game.COLOR_NONE ?
              'game-player' :
              winner == Game.COLOR_WHITE ?
                'game-player-winner' :
                'game-player'
            }
          >{whitePlayerName}</div>
        </div>
        <Board
          gameMode={this.state.gameMode}
          isPlacementSending={this.state.isPlacementSending}
          isPlayerSending={this.state.isPlayerSending}
          isDescRead={this.state.isDescRead}
          onClick={(e, skip, color, x, y) => this.createPlacement(e, skip, color, x, y)}
        />
        <div className="game-message">{message}</div>
        <div id="chat">
          <Chat/>
          <ChatForm
            name={this.state.playerName}
           />
        </div>
      </Fragment>
    );
  }
}

const judgeGomoku = (allPlacements) => {
  // マス分の配列を用意して、碁石があるマスだけboolが入る
  let cells = Array.from(new Array(Game.WIDTH * Game.HEIGHT)).map(() => Game.COLOR_NONE);
  for (const placement of allPlacements) {
    cells[placement.x + placement.y * Game.WIDTH] = placement.color;
  }

  const FIVE = 5;
  const TWO = Math.floor((FIVE - 1) / 2);

  for (const [index, color] of cells.entries()) {
    if (color != Game.COLOR_NONE) {
      const x = index % Game.WIDTH;
      const y = Math.floor(index / Game.WIDTH);

      // 縦、横、斜め*2を順に、5マス分調べる
      let result;

      // たて
      result = 0;
      for (
        let targetIndex = index - Game.WIDTH * TWO;
        targetIndex <= index + Game.WIDTH * TWO && 0 < targetIndex;
        targetIndex += Game.WIDTH
      ) {
        if (color == cells[targetIndex]) {
          result++;
        }
      }
      if (result == FIVE) {
        return color;
      }

      // よこ
      result = 0;
      for (
        let targetIndex = index - TWO;
        targetIndex <= index + TWO && 0 < targetIndex;
        targetIndex ++
      ) {
        if (color == cells[targetIndex]) {
          result++;
        }
      }
      if (result == FIVE) {
        return color;
      }

      // 左上から右下のななめ
      result = 0;
      for (
        let targetIndex = index - Game.WIDTH * TWO - TWO;
        targetIndex <= index + Game.WIDTH * TWO + TWO && 0 < targetIndex;
        targetIndex += Game.WIDTH + 1
      ) {
        if (color == cells[targetIndex]) {
          result++;
        }
      }
      if (result == FIVE) {
        return color;
      }

      // 右上から左下のななめ
      result = 0;
      for (
        let targetIndex = index - Game.WIDTH * TWO + TWO;
        targetIndex <= index + Game.WIDTH * TWO - TWO && 0 < targetIndex;
        targetIndex += Game.WIDTH - 1
      ) {
        if (color == cells[targetIndex]) {
          result++;
        }
      }
      if (result == FIVE) {
        return color;
      }
    }
  }

  return Game.COLOR_NONE;
}

const PLAYERS_QUERY = gql`
  query {
    blackPlayer: allPlayers(
      filter: {
        color: false
      },
      last: 1
    ) {
      name
    }
    whitePlayer: allPlayers(
      filter: {
        color: true
      },
      last: 1
    ) {
      name
    }
  }
`;

const PLAYERS_SUBSCRIPTION = gql`
  subscription updatePlayer {
    Player(
      filter: {
        mutation_in: [CREATED]
      }
    ) {
      mutation
      node {
        color,
        name
      }
    }
  }
`;

const CREATE_PLACEMENT_MUTATION = gql`
  mutation createPlacementMutation($color: Boolean!, $x: Int!, $y: Int!) {
    createPlacement(color: $color, x: $x, y: $y) {
      id
    }
  }
`;

const CREATE_PLAYER_MUTATION = gql`
  mutation createPlayerMutation($color: Boolean, $name: String) {
    createPlayer(color: $color, name: $name) {
      id
    }
  }
`;

const ALL_PLACEMENTS_QUERY = gql`
  query {
    allPlacements {
      id
      color
      x
      y
    }
  }
`;

const PLACEMENTS_SUBSCRIPTION = gql`
  subscription updatePlacement {
    Placement(
      filter: {
        mutation_in: [CREATED, DELETED]
      }
    ) {
      mutation
      node {
        id
      }
    }
  }
`;

const RESET_BOARD_MUTATION = gql`
  mutation ResetBoardMutation($id: ID!) {
    deletePlacement(id: $id) {
      id
    }
  }
`;

export default compose(
  graphql(PLAYERS_QUERY, {
    name : 'playersQuery'
  }),
  graphql(CREATE_PLACEMENT_MUTATION, {
    name : 'createPlacementMutation'
  }),
  graphql(CREATE_PLAYER_MUTATION, {
    name: 'createPlayerMutation'
  }),
  graphql(RESET_BOARD_MUTATION, {
    name: 'ResetBoardMutation'
  }),
  graphql(ALL_PLACEMENTS_QUERY, {
    name: 'allPlacementsQuery'
  })
)(Game);



