
// マスをレンダリングする

import React from 'react';

import Game from "./Game";

function Cell(props) {
  let markClass = 'cell-mark color-none';
  if (props.color !== Game.COLOR_NONE) {
    markClass = (props.color === Game.COLOR_WHITE) ?
      'cell-mark color-white' :
      'cell-mark color-black';
  }

  return (
    <div className="cell" onClick={(e) => props.onClick(e, props.x, props.y)}>
      <div className={markClass}></div>
    </div>
  );
}

export default Cell;



